@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700&family=Poppins:wght@200;300;400;500;600;700&display=swap");

*,
*::before,
*::after {
	box-sizing: border-box;
}

* {
	margin: 0;
	padding: 0;
	font-family: "Poppins", sans-serif;
}

.App {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

/*Navbar Section*/
.navbar {
	display: flex;
	align-items: center;
	background: linear-gradient(to right, purple, mediumpurple);
	width: 100vw;
	padding: 1rem 2rem;
	color: #fff;
	box-shadow: rgba(17, 17, 26, 0.1) 0px 1px 0px,
		rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 48px;
}

.nav-logo {
	width: 60px;
	margin-right: 0.7rem;
}

.nav-brand {
	letter-spacing: 0.1rem;
	font-size: 1.75rem;
}

/*Input Area*/
.input-area {
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 5rem 0;
}

.input {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-bottom: 2rem;
}

.input > input {
	padding: 0.5rem;
	font-size: 1.1rem;
	width: 48%;
	border: 1px solid mediumpurple;
	border-radius: 5px;
	box-shadow: rgba(17, 17, 26, 0.1) 0px 1px 0px,
		rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 48px;
}

.input-area > form > button {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	font-size: 1.25rem;
	border: none;
	padding: 0.25rem;
	background: linear-gradient(to right, purple, mediumpurple);
	color: #fff;
	border-radius: 25px;
	box-shadow: rgba(0, 0, 0, 0.2) 0px 20px 30px;
	transition: box-shadow 0.1s ease-in-out;
}

.input-area > form > button:hover {
	transform: scale(0.99);
	box-shadow: rgba(17, 17, 26, 0.1) 0px 1px 0px,
		rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 48px;
}

.input-area svg {
	margin-left: 1rem;
}

/*Output Section*/
.output {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	max-width: 700px;
	max-height: 400px;
	background: linear-gradient(to right, purple, mediumpurple);
	box-shadow: rgba(17, 17, 26, 0.1) 0px 1px 0px,
		rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 48px;
}

.output > img {
	max-width: 700px;
	max-height: 400px;
}

.top-text {
	max-width: 600px;
	color: #fff;
	font-size: 2rem;
	position: absolute;
	top: 5%;
	text-align: center;
}

.bottom-text {
	max-width: 600px;
	color: #fff;
	font-size: 2rem;
	position: absolute;
	bottom: 5%;
	text-align: center;
}
